import {Routes} from '@angular/router';
import {permissionGuard} from '@core/auth/guards/permission.guard';
import {UserList} from './users/pages/user-list/user-list';
import {UserForm} from './users/pages/user-form/user-form';
import {RoleList} from './roles/pages/role-list/role-list';
import {RoleForm} from './roles/pages/role-form/role-form';
import {PermissionList} from './permissions/pages/permission-list/permission-list';
import {PermissionForm} from './permissions/pages/permission-form/permission-form';
import {Settings} from './settings/pages/settings/settings';
import {DepartmentList} from './departments/pages/department-list/department-list';
import {DepartmentForm} from './departments/pages/department-form/department-form';
import {JobTitleList} from './job-titles/pages/job-title-list/job-title-list';
import {JobTitleForm} from './job-titles/pages/job-title-form/job-title-form';
import {ApprovalList} from './approvals/pages/approval-list/approval-list';
import {ApprovalFlow} from './approvals/pages/approval-flow/approval-flow';
import {ProjectList} from './projects/pages/project-list/project-list';
import {ProjectForm} from './projects/pages/project-form/project-form';
import {PaymentList} from './payment-requests/pages/payment-list/payment-list';
import {PaymentForm} from './payment-requests/pages/payment-form/payment-form';
import {ApprovalTaskList} from './approval-tasks/pages/approval-task-list/approval-task-list';
import {ApprovalTaskReview} from './approval-tasks/pages/approval-task-review/approval-task-review';
import {LeaveRequestList} from './leave-requests/pages/leave-request-list/leave-request-list';
import {LeaveRequestForm} from './leave-requests/pages/leave-request-form/leave-request-form';
import {TravelRequestList} from './travel-requests/pages/travel-request-list/travel-request-list';
import {TravelRequestForm} from './travel-requests/pages/travel-request-form/travel-request-form';

export const ADMIN_ROUTES: Routes = [
  {path: '', redirectTo: 'users', pathMatch: 'full'},

  // 
  {path: 'users',                component: UserList,       data: {title: ''}},
  {path: 'users/new',            component: UserForm,       data: {title: ''}},
  {path: 'users/:id/edit',       component: UserForm,       data: {title: ''}},

  // 
  {path: 'departments',          component: DepartmentList, data: {title: ''}},
  {path: 'departments/new',      component: DepartmentForm, data: {title: ''}},
  {path: 'departments/:id/edit', component: DepartmentForm, data: {title: ''}},

  // 
  {path: 'job-titles',           component: JobTitleList,   data: {title: ''}},
  {path: 'job-titles/new',       component: JobTitleForm,   data: {title: ''}},
  {path: 'job-titles/:id/edit',  component: JobTitleForm,   data: {title: ''}},

  // 
  {path: 'approvals',            component: ApprovalList,   data: {title: ''}},
  {path: 'approvals/:id/flow',   component: ApprovalFlow,   data: {title: ''}},

  //  / 
  {path: 'roles',                component: RoleList,       canActivate: [permissionGuard], data: {title: '',   permission: 'superadmin'}},
  {path: 'roles/new',            component: RoleForm,       canActivate: [permissionGuard], data: {title: '',   permission: 'superadmin'}},
  {path: 'roles/:id/edit',       component: RoleForm,       canActivate: [permissionGuard], data: {title: '',   permission: 'superadmin'}},
  {path: 'permissions',          component: PermissionList, canActivate: [permissionGuard], data: {title: '',   permission: 'superadmin'}},
  {path: 'permissions/new',      component: PermissionForm, canActivate: [permissionGuard], data: {title: '',   permission: 'superadmin'}},
  {path: 'permissions/:id/edit', component: PermissionForm, canActivate: [permissionGuard], data: {title: '',   permission: 'superadmin'}},

  // 
  {path: 'projects',             component: ProjectList,    data: {title: ''}},
  {path: 'projects/new',         component: ProjectForm,    data: {title: ''}},
  {path: 'projects/:id/edit',    component: ProjectForm,    data: {title: ''}},

  // 
  {path: 'payment-requests',             component: PaymentList, data: {title: ''}},
  {path: 'payment-requests/new',         component: PaymentForm, data: {title: ''}},
  {path: 'payment-requests/:id/edit',    component: PaymentForm, data: {title: ''}},

  // 
  {path: 'leave-requests',             component: LeaveRequestList, data: {title: ''}},
  {path: 'leave-requests/new',         component: LeaveRequestForm, data: {title: ''}},
  {path: 'leave-requests/:id/edit',    component: LeaveRequestForm, data: {title: ''}},

  // 
  {path: 'travel-requests',             component: TravelRequestList, data: {title: ''}},
  {path: 'travel-requests/new',         component: TravelRequestForm, data: {title: ''}},
  {path: 'travel-requests/:id/edit',    component: TravelRequestForm, data: {title: ''}},

  // 
  {path: 'approval-tasks',                                   component: ApprovalTaskList,   data: {title: ''}},
  {path: 'approval-tasks/:applicationType/:id/review',       component: ApprovalTaskReview, data: {title: ''}},

  // 
  {path: 'settings',             component: Settings,       data: {title: ''}},
];
